import React from "react"
import Img from "gatsby-image"
import { useStaticQuery, graphql } from "gatsby"

import mockupAgodaImage from "../../images/mockupAgoda.png"
import styles from "./agoda.module.scss"
import EmptyLine from "../../components/empty-line"

export default () => {
  const data = {
    title: "Agoda",
    subTitle: "Hotel and Flight Booking Mobile App",
  }
  const {
    agodaBanner,
    agodaImage1,
    agodaImage2,
    agodaImage3,
    agodaImage4,
    agodaImage5,
    agodaImage6,
  } = useStaticQuery(graphql`
    query {
      agodaBanner: file(relativePath: { eq: "agodaBanner.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage1: file(relativePath: { eq: "agodaImage1.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage2: file(relativePath: { eq: "agodaImage2.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage3: file(relativePath: { eq: "agodaImage3.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage4: file(relativePath: { eq: "agodaImage4.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage5: file(relativePath: { eq: "agodaImage5.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agodaImage6: file(relativePath: { eq: "agodaImage6.png" }) {
        childImageSharp {
          fluid(maxWidth: 960) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <div>
      <div className={styles.secondaryBackgroundColor}>
        <img
          src={mockupAgodaImage}
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className="container">
        <div className={styles.pageMainContentSection}>
          <div className={styles.pageMainContent}>
            <EmptyLine />
            <div>
              <div className={styles.projectTitle}>{data.title}</div>
              <div className={styles.projectSubTitle}>{data.subTitle}</div>
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectText}>
              <div>
                Agoda is a popular mobile app for booking hotels and flights
                around the world.
              </div>
              <EmptyLine variant="Paragraph" />
              <div>
                The current app is easy to use but I notice that sometimes it
                looks a little bit messy. The dark mode is in progress so there
                is no much difference in UI between light and dark mode.
              </div>
              <EmptyLine variant="Paragraph" />
              <div>
                I redesign the Agoda app with a clearer layout, obvious dark
                mode, and I also work on my idea to let users decide the primary
                color appeared in the app among the five colors in the Agoda
                logo.
              </div>
              <EmptyLine variant="Paragraph" />
              <div className="columns is-gapless">
                <div className="column">
                  <div className="columns is-gapless is-mobile">
                    <div className={`column ${styles.labelWithGreenLine}`}>
                      <span className={styles.projectSubTitleWithoutTextAlign}>
                        Current app
                      </span>
                      <div className={styles.greenLine} />
                    </div>
                    <div className={`column is-narrow ${styles.gapHelper}`} />
                    <div className={`column ${styles.labelWithGreenLine}`}>
                      <span className={styles.projectSubTitleWithoutTextAlign}>
                        New version
                      </span>
                      <div className={styles.greenLine} />
                    </div>
                  </div>
                </div>
                <div className="column is-hidden-mobile" />
              </div>
              <EmptyLine variant="Paragraph" />
              <div className="columns is-gapless">
                <div className="column">
                  <div className="columns is-gapless is-mobile">
                    <div className="column">
                      <Img fluid={agodaImage1.childImageSharp.fluid} alt="" />
                    </div>
                    <div className={`column is-narrow ${styles.gapHelper}`} />
                    <div className="column">
                      <Img fluid={agodaImage2.childImageSharp.fluid} alt="" />
                    </div>
                  </div>
                </div>
                <div className="column">
                  <div className="columns is-gapless">
                    <div
                      className={`column is-narrow is-hidden-mobile ${styles.gapHelper}`}
                    />
                    <div className={`column ${styles.rowTextSection}`}>
                      <div>
                        - In the current app homepage, the users can only input
                        data for hotel search. If they want to book flights,
                        they have to scroll down to the Travel services part at
                        the bottom.
                      </div>
                      <EmptyLine variant="Paragraph" />
                      <div>
                        - In the new version, I provide 3 tabs: booking for
                        hotels, flights, and combo flight+hotel. This helps
                        users to see all of their options on the top of the
                        screen and save time scrolling down.
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <EmptyLine />
              <div className="columns is-gapless">
                <div className="column is-hidden-mobile" />
                <div className="column">
                  <div className="columns is-gapless">
                    <div
                      className={`column is-narrow is-hidden-mobile ${styles.gapHelper}`}
                    />
                    <div className="column">
                      <div className={styles.greenLine} />
                    </div>
                  </div>
                </div>
              </div>
              <EmptyLine />
              <div className="columns is-gapless">
                <div className="column">
                  <div className="columns is-gapless is-mobile">
                    <div className="column">
                      <Img fluid={agodaImage3.childImageSharp.fluid} alt="" />
                    </div>
                    <div className={`column is-narrow ${styles.gapHelper}`} />
                    <div className="column">
                      <Img fluid={agodaImage4.childImageSharp.fluid} alt="" />
                    </div>
                  </div>
                </div>
                <div className="column">
                  <div className="columns is-gapless">
                    <div
                      className={`column is-narrow is-hidden-mobile ${styles.gapHelper}`}
                    />
                    <div className={`column ${styles.rowTextSection}`}>
                      <div>
                        - Dark mode of the current app seems to be on progress
                        so there is no much change from light to dark mode,
                        except for the darker background.
                      </div>
                      <EmptyLine variant="Paragraph" />
                      <div>
                        - In the new version, dark mode is more obvious with
                        updated background, surface, and text color.
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <EmptyLine />
              <div className="columns is-gapless">
                <div className="column is-hidden-mobile" />
                <div className="column">
                  <div className="columns is-gapless">
                    <div
                      className={`column is-narrow is-hidden-mobile ${styles.gapHelper}`}
                    />
                    <div className="column">
                      <div className={styles.greenLine} />
                    </div>
                  </div>
                </div>
              </div>
              <EmptyLine />
              <div className="columns is-gapless">
                <div className="column">
                  <div className="columns is-gapless is-mobile">
                    <div className="column">
                      <Img fluid={agodaImage5.childImageSharp.fluid} alt="" />
                    </div>
                    <div className={`column is-narrow ${styles.gapHelper}`} />
                    <div className="column">
                      <Img fluid={agodaImage6.childImageSharp.fluid} alt="" />
                    </div>
                  </div>
                </div>
                <div className="column">
                  <div className="columns is-gapless">
                    <div
                      className={`column is-narrow is-hidden-mobile ${styles.gapHelper}`}
                    />
                    <div className={`column ${styles.rowTextSection}`}>
                      <div>
                        - In the current setting page, some information look
                        messy and unnecessary. I have used this app for more
                        than 4 years but I do not know what Agoda Reception and
                        PointMAX are, and what the difference between Promotions
                        and Coupons&amp;Deals is.
                      </div>
                      <EmptyLine variant="Paragraph" />
                      <div>
                        - In the new version, users can toggle between light or
                        dark mode, especially, they can choose the primary color
                        among the five colors based on the Agoda logo. Settings
                        about language, price display, measurement, etc. appear
                        in the Edit Profile section on the top.
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <EmptyLine />
            </div>
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <Img fluid={agodaBanner.childImageSharp.fluid} alt="" />
      </div>
    </div>
  )
}
